// Typgraphy
$heading-1: 5rem;
$heading-2: 4rem;
$heading-3: 3rem;
$heading-4: 2rem;
$heading-5: 1rem;

$default-1: 2.5rem;
$default-2: 2rem;
$default-3: 1.5rem;
$default-4: 1rem;

// Spacing
$spacer-large: 3.5rem;
$spacer-med: 2.5rem;
$spacer-small: 1.5rem;

// Padding
$padding-button: 0.75rem 1.5rem;
$padding: 2rem 3rem;

$spacer: 3rem;
$round-edges: 2000px;

// Shadow
$box-shadow-light: 0 1.5rem 1rem rgba(black, 0.1);
$box-shadow-med: 0 1.5rem 2rem rgba(black, 0.3);
$box-shadow-dark: 0 1.5rem 4rem rgba(black, 0.5);

// Mobile
$mobile-width: 600px;
